	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2016-05-18-google-io/index.md absPath of file >>> MarkdownRemark","html":"<div class=\"post-images\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-db0af.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"google io crowd\" title=\"\" src=\"/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-b80fa.jpg\" srcset=\"/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-cf410.jpg 163w,\n/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-62f2a.jpg 325w,\n/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-b80fa.jpg 650w,\n/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-08cb4.jpg 975w,\n/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-f2bf9.jpg 1300w,\n/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-fd4ba.jpg 1950w,\n/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-db0af.jpg 3264w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-db0af.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"google io at night\" title=\"\" src=\"/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-b80fa.jpg\" srcset=\"/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-cf410.jpg 163w,\n/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-62f2a.jpg 325w,\n/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-b80fa.jpg 650w,\n/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-08cb4.jpg 975w,\n/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-f2bf9.jpg 1300w,\n/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-fd4ba.jpg 1950w,\n/static/google_io_night-4712087e5aee27d8c2c2107d36a834eb-db0af.jpg 3264w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-db0af.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"scratch working with legos\" title=\"\" src=\"/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-b80fa.jpg\" srcset=\"/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-cf410.jpg 163w,\n/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-62f2a.jpg 325w,\n/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-b80fa.jpg 650w,\n/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-08cb4.jpg 975w,\n/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-f2bf9.jpg 1300w,\n/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-fd4ba.jpg 1950w,\n/static/scratch_lego-ed5d9a7dbe52d1aa66324c8af60adb9e-db0af.jpg 3264w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n</div>\n<p>Google IO day one is over and my feet are killing me. Here are my key take aways for the day:</p>\n<h1>Firebase</h1>\n<p>Firebase has become a key component in the Google ecosystem. What was once a real time database is now a platform for your entire infrastructure and application business development needs. Today there were plenty of comparisons to Parse, Facebook's ill-fated application platform. Since I'm at Google IO I didn't hear many comparisons to Amazon's ecosystem of products, but I'm sure others will make the comparison. Amazon does offer more in technical functionality but at the cost of complexity. Though AWS offers an inexpensive and powerful compute platform they won't be winning any awards for developer experience. Google has done a great job considering not just the technical but also the business requirements for the entire application lifecycle. Developers still have the same powerful real time database as a service that they've had for a long time, but now the product covers the deployment process that includes hosting with SSL and free custom domains. File uploads are one line of code simple and though authentication doesn't change a lot it's now backed by the same processes that Google uses for OAuth. Authentication is hard. Firebase makes it simple. Application testing and crash reporting mean fewer bugs make it to production and those that do can be dealt with quickly. Remote configuration allows for testing and implementation of features based on unlimited free analytics provide the business with information to make key decisions. Adwords, invites, dynamic links and app indexing make it super simple to grow your user base. If you don't have customers your application will die. Everyone else leaves this step as an exercise for the reader. Customers typically lead to revenue and so Firebase brings AdMob to your application so you can start earning right away.</p>\n<p>Full disclosure, I'm a Firebase Google Developer Expert so if you need help implementing Firebase my company, <a href=\"http://www.atomicjolt.com\">Atomic Jolt</a> is ready to help. It also means I get one of these:</p>\n<div class=\"post-images\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-db0af.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 650px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Firebase Google Developer Expert shirt\" title=\"\" src=\"/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-b80fa.jpg\" srcset=\"/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-cf410.jpg 163w,\n/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-62f2a.jpg 325w,\n/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-b80fa.jpg 650w,\n/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-08cb4.jpg 975w,\n/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-f2bf9.jpg 1300w,\n/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-fd4ba.jpg 1950w,\n/static/firebase_shirt-eb8c6685eef4334cd9198238d073e278-db0af.jpg 3264w\" sizes=\"(max-width: 650px) 100vw, 650px\">\n    </span>\n  </span>\n  \n  </a>\n    \n</div>\n<h1>Google Home</h1>\n<p>I have an Amazon Echo and I love it, but it's pretty stupid. You have to be careful how you word your questions. I built a skill for it. It's cool that Amazon is putting a lot of effort into encouraging developers. It's cool that it works with my Ecobee and with my Philips Hue lights. I like it so much that I bought another Echo and an Echo Bee. However, both of them are going back. Unless Amazon can significantly improve Alexa's IQ by Fall I'll be making the switch. Google's Home device might look like an overgrown Glade plugin but I don't care what it looks like if it case Google's compute power behind it. Who knows if it will perform like the demo they showed, but if it does it will enable intelligent interaction our homes.</p>\n<h1>Some Personal Favorites</h1>\n<ol>\n<li>The LLK (Life Long Kindergarten) group at MIT's media lab are working with Google to bring Scratch to mobile devices with a new <a href=\"https://github.com/LLK/scratch-blocks\">open source project called Scratch Blocks</a>. They also showed off cardboard bots with the caveat that they were very expensive and so not likely to be available to schools. I'll give a shout out to <a href=\"http://cachemakers.org/\">Cache Maker's</a> who did something similar for a couple of bucks using the ESP2866.</li>\n<li>Tyler McGinnis' <a href=\"https://github.com/tylermcginnis/re-base\">re-base</a> library that brings Firebase and React together in a Relay-like way.\n3 The California weather is amazing. There's been plenty of complaint about the venue being outside and I've seen a few people who clearly forgot to put on sunscreen. However, I spend plenty of time indoors. It's nice to get out and enjoy the world outside.</li>\n</ol>\n<h1>Less Favorite</h1>\n<ol>\n<li>I have to remember that the food is high calorie, zero nutrition garbage. Hot dogs, fries and mac and cheese with soda do not make a meal.</li>\n<li>The lines are long. So long. There are 7,000 people here. You wait to get into everything. The most popular sessions fill up fast.</li>\n<li>There's no power. Google tells you there will be but there's not. Conserve your juice. What you bring is what you get.</li>\n</ol>","frontmatter":{"title":"Google IO Day 1","date":"May 18, 2016","imageUrl":null,"image":{"childImageSharp":{"sizes":{"src":"/static/google_io_crowd-e77b279e3c083e6226000636e66ac9c3-381d6.jpg"}}}}}},"pathContext":{"path":"/google-io"}}

